<template>
  <div v-if="siteData" class="site-container">
    <div
      class="px-4 py-5 section mx-auto sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-2 lg:px-1 lg:py-5 mt-5"
    >
      <div class="max-w-xl mb-10 md:mx-auto sm:text-center lg:max-w-2xl md:mb-12">
        <div class="text-center">
          <p
            class="inline-block px-3 py-px mb-4 text-4xl tracking-wider text-teal-900 uppercase rounded-full bg-teal-accent-400"
          >
            設備展示
          </p>
        </div>
      </div>

      <!-- Carousel Container -->
      <div class="carousel">
        <div class="carousel-container">
          <transition-group name="carousel-slide" tag="div" class="carousel-track">
            <div
              v-for="(image, index) in visibleImages"
              :key="index"
              class="carousel-item gap-2"
            >
              <img
                class="object-cover w-full h-56 rounded shadow-lg hover:scale-125 transition duration-500 cursor-pointer"
                :src="getUrl(image.image)"
                alt=""
              />
            </div>
          </transition-group>
        </div>
        <button class="carousel-button prev" @click="prevSlide">&#10094;</button>
        <button class="carousel-button next" @click="nextSlide">&#10095;</button>
      </div>
    </div>
  </div>
</template>

<script>
import env from "@/env";

const baseURL = env.baseURL;

export default {
  components: {},
  data() {
    return {
      siteData: null,
      slideIndex: 0,
    };
  },
  async mounted() {
    try {
      let { data } = await this.axios.get(`public/site-data`);
      this.siteData = data.data;
      this.isMounted = true;
      console.log(data);

      // Initialize Carousel
      this.initCarousel();
    } catch (e) {
      let { data: { message } } = e.response;
      this.error = message;
      this.$notify({ title: "Error", text: message });
    }
  },
  computed: {
    visibleImages() {
      if (!this.siteData || !this.siteData.equipmentImages.length) {
        return [];
      }

      const numImages = this.siteData.equipmentImages.length;
      const startIndex = this.slideIndex % numImages;
      const endIndex = (this.slideIndex + 3) % numImages;

      const images = [...this.siteData.equipmentImages];

      if (startIndex < endIndex) {
        return images.slice(startIndex, endIndex + 1);
      } else {
        return [...images.slice(startIndex), ...images.slice(0, endIndex + 1)];
      }
    },
  },
  methods: {
    getUrl(image) {
      if (!image) return require("@/assets/images/no_image_added.jpg");
      return `${baseURL()}uploads/${image}`;
    },
    initCarousel() {
      setInterval(this.nextSlide, 3000); // Auto-advance to the next slide every 3 seconds
    },
    nextSlide() {
      this.slideIndex++;
    },
    prevSlide() {
      this.slideIndex--;
      if (this.slideIndex < 0) {
        this.slideIndex = this.siteData.equipmentImages.length - 1;
      }
    },
  },
};
</script>

<style scoped>
.carousel {
  position: relative;
  overflow: hidden;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
}

.carousel-container {
  display: flex;
  transition: transform 0.5s;
  overflow: hidden;
}

.carousel-track {
  display: flex;
  justify-content: flex-start; /* Add this line */
}

.carousel-item {
  flex: 0 0 calc(25% - 20px); /* Display four items at once with a 20px gap between them */
  margin-right: 20px; /* Add this line for the gap */
  box-sizing: border-box;
}

.carousel-item:last-child {
  margin-right: 0; /* Remove the margin on the last item */
}

.carousel-image {
  width: 100%;
  height: auto;
  display: block;
}

.carousel-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 40px;
  height: 40px;
  background-color: rgba(0, 0, 0, 0.3);
  border: none;
  color: white;
  font-size: 24px;
  text-align: center;
  cursor: pointer;
  outline: none;
  user-select: none;
  transition: background-color 0.3s;
}

.carousel-button:hover {
  background-color: rgba(0, 0, 0, 0.6);
}

.carousel-button.prev {
  left: 0;
}

.carousel-button.next {
  right: 0;
}

.carousel-slide-enter-active,
.carousel-slide-leave-active {
  transition: opacity 0.5s;
}

.carousel-slide-enter, .carousel-slide-leave-to /* .carousel-slide-leave-active in <2.1.8 */ {
  opacity: 0;
}
</style>
