<template>
    <div class="carousel-container">


        <h1
            class="mb-4 text-xl mt-10  uppercase text-center  font-sans tracking-tight leading-none text-gray-800 md:text-2xl lg:text-4xl dark:text-white">
            博士紅感謝全省合作客戶</h1>
        <p class="mb-8 text-lg font-normal text-center text-red-500 lg:text-xl sm:px-16 lg:px-48 dark:text-gray-400">
            （歡迎全省客戶全國廠商來圖刊登公司有權修改及下架不宜內容）</p>
        <div class="carousel">
            <transition name="carousel-slide" mode="out-in">
                <div :key="currentIndex" class="carousel-slide">
                    <div class="slide" v-for="(banner, index) in visibleBanners" :key="index">
                        <div class="slide-item">
                            <div class="relative pb-56 mb-4 rounded shadow lg:pb-64">
                                <img class="absolute object-contain w-full h-full rounded" :src="getUrl(banner.image)"
                                    alt="Person" />
                            </div>
                            <div class="flex flex-col sm:text-center">
                                <p class="text-lg font-sans">{{ banner.text }}</p>
                            </div>
                        </div>
                    </div>
                </div>
            </transition>
            <button class="prev" @click="prevSlide">&#10094;</button>
            <button class="next" @click="nextSlide">&#10095;</button>
        </div>

        <div
            class="relative mt-5 px-4 py-16 mx-auto border-0 shadow-xl sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-24 lg:px-0 lg:py-0">
            <img class="object-cover w-full h-full sm:h-96  " :src="getUrl(mainBanner?.image)" alt="" />
            <p class="text-center py-2">{{mainBanner?.text}}</p>
        </div>
    </div>
</template>
  
<script>
import env from '@/env'
const baseURL = env.baseURL

export default {
    props: ['previousBanners'],
    data() {
        return {
            banners: [],
            currentIndex: 0,
            mainBanner: null
        }
    },
    computed: {
        visibleBanners() {
            const start = this.currentIndex
            const end = this.currentIndex + 4

            // Use a circular loop when reaching the end of the banners
            return [...this.banners.slice(start, end), ...this.banners.slice(0, start)]
        },
    },
    mounted() {
        let allBanners = this.previousBanners
        let lastBanner = allBanners.pop()
        this.banners = allBanners
        this.banners.unshift(lastBanner) // Add the last banner to the beginning
        this.mainBanner = lastBanner
        // Auto-advance to the next slide every 3 seconds
        setInterval(this.nextSlide, 3000)
    },
    methods: {
        getUrl(image) {
            if (!image) return require('@/assets/images/no_image_added.jpg')
            return `${baseURL()}uploads/${image}`
        },
        nextSlide() {
            this.currentIndex = (this.currentIndex + 1) % this.banners.length
        },
        prevSlide() {
            this.currentIndex =
                (this.currentIndex - 1 + this.banners.length) % this.banners.length
        },
    },
}
</script>
  
